/* Responsive */
@media (max-width: 800px) {
    /* Block */
    .form__instruction {
        width: 170px;
    }

    /* Element */
    .form__to-fill {
        width: calc(100% - 170px);
        padding-left: 0;
    }

    .form__animation {
        width: calc(100% - 170px);
        left: 170px;
    }

}

@media (max-width: 550px) {

    /* Block */
    .form {
        position: relative;
        transform: none;
        padding: 0 20px;
    }

    .form__informations {
        margin-bottom: 0;
    }

    .form__bottom {
        padding-left: 0;
        padding-right: 0;
    }

    /* Elements */
    .form__item {
        display: block;
        margin-bottom: 30px;
        padding-left: 0;
        padding-right: 0;
    }

    .form__instruction {
        width: 100%;
        margin-bottom: 20px;
        display: block;
    }

    .form__to-fill {
        width: 100%;
        padding-left: 0;
        display: block;
    }

    .form__to-fill--textarea {
        height: auto;
    }

    .form__animation {
        left: 0;
        height: 5px;
        width: 100%;
    }
}